import React, { useEffect, useState } from 'react'
import { Container, Table } from 'react-bootstrap'
import Staff from './Staff'
import axios from 'axios'

const Index = () => {
  const [visits, setVisits] = useState(0)
  const [upi, setUpi] = useState(0)
  const [cash, setCash] = useState(0)
  const [todayIncome, setTodayIncome] = useState(0)

  const todayVisits = async () => {
    try {
      const res = await axios.get('/api/v1/admin/getTodayVisits')
      if (res.data.success) {
        setVisits(res.data.data)
      }
    } catch (error) {
      console.log(error)
    }
  }
  const getTodayIncomeInCash = async () => {
    try {
      const res = await axios.get('/api/v1/admin/getTodayIncomeInCash')
      if (res.data.success) {
        setCash(res.data.data)
      }
    } catch (error) {
      console.log(error)
    }
  }
  const getTodayIncomeInUpi = async () => {
    try {
      const res = await axios.get('/api/v1/admin/getTodayIncomeInUpi')
      if (res.data.success) {
        setUpi(res.data.data)
      }
    } catch (error) {
      console.log(error)
    }
  }
  const getTodayIncome = async () => {
    try {
      const res = await axios.get('/api/v1/admin/getTodaysIncome')
      if (res.data.success) {
        setTodayIncome(res.data.data)
      }
    } catch (error) {
      console.log(error)
    }
  }

  useEffect(() => {
    todayVisits()
    getTodayIncomeInCash()
    getTodayIncomeInUpi()
    getTodayIncome()
  }, [])
  return (
    <Container>
      <div
        style={{
          height: '20%',
          width: '100%',
        }}
        className="d-flex text-white mb-3"
      >
        <div
          style={{
            backgroundColor: '#fe9496',
          }}
          className="rounded p-3 col-12 col-md-3"
        >
          <div>
            <h3>Visits Today</h3>
            <p>{visits && visits}</p>
          </div>
        </div>
        <div className="rounded bg-info p-3 col-12 col-md-5 mx-3">
          <div>
            <h3>Transactions Today</h3>
            <Table responsive>
              <thead>
                <tr>
                  <th className="text-center">By Cash</th>
                  <th className="text-center">By UPI</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td className="text-center">{cash && cash}</td>
                  <td className="text-center">{upi && upi}</td>
                </tr>
              </tbody>
            </Table>
          </div>
        </div>
        <div
          style={{
            backgroundColor: 'black',
          }}
          className="rounded p-3 col-12 col-md-3"
        >
          <div>
            <h3>Income Today</h3>
            <p>{todayIncome && todayIncome} Rs.</p>
          </div>
        </div>
      </div>
      {/* <div className="d-flex mt-3">
        <Card className="col-12 col-md-6">
          <Card.Header>Doctors</Card.Header>
          <Card.Body>
            <Table responsive>
              <thead>
                <tr>
                  <th>Sn</th>
                  <th>Name</th>
                  <th>Department</th>
                  <th>Status</th>
                </tr>
                <tbody>

                </tbody>
              </thead>
            </Table>
          </Card.Body>
        </Card>
        <Card className="col-12 col-md-6">
          <Card.Header>Receptionist</Card.Header>
          <Card.Body>
            <Table responsive>
              <thead>
                <tr>
                  <th>Sn</th>
                  <th>Name</th>
                  <th>Department</th>
                  <th>Status</th>
                </tr>
                <tbody>{receptionists && receptionists.length > 0 ? (
                    receptionists.map((receptionist, idx) => {
                      return (
                        <tr className="text-center" key={idx}>
                          <td>{idx + 1}</td>
                          <td>{receptionist.name}</td>
                          <td>{receptionist.uniqueId}</td>
                        </tr>
                      )
                    })
                  ) : (
                    <tr>
                      <td colSpan="4" className="text-center">
                        No Receptionist Found
                      </td>
                    </tr>
                  )}</tbody>
              </thead>
            </Table>
          </Card.Body>
        </Card>
      </div> */}
      <Staff />
    </Container>
  )
}

export default Index
